'use client';

import { GlassCard } from '@/components/ui/GlassCard';
import { AlertTriangle, ArrowRight, Loader2, X, Filter, ArrowUpDown, Eye, Calendar, Gauge, Ruler, Maximize2 } from 'lucide-react';
import { clsx } from 'clsx';
import { useEffect, useState, useMemo } from 'react';
import { useSession } from 'next-auth/react';
import { useRouter } from 'next/navigation';

interface Neo {
    id: string;
    name: string;
    date: string;
    distanceVal: number; // raw value for sorting
    distance: string;
    velocityVal: number; // raw value for sorting
    velocity: string;
    diameterVal: number; // raw value for sorting
    diameter: string;
    isHazardous: boolean;
    absolute_magnitude: number;
}

export function NeoFeed() {
    const { data: session } = useSession();
    const router = useRouter();
    const [neos, setNeos] = useState<Neo[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    // Filters & Sorting
    const [filterHazardous, setFilterHazardous] = useState(false);
    const [filterSize, setFilterSize] = useState<'all' | 'small' | 'medium' | 'large'>('all');
    const [sortBy, setSortBy] = useState<'diameter' | 'velocity' | 'distance' | 'date'>('diameter');

    // Details Modal
    const [selectedNeo, setSelectedNeo] = useState<Neo | null>(null);

    useEffect(() => {
        const fetchNeos = async () => {
            try {
                const res = await fetch('/api/neos');
                if (!res.ok) throw new Error('Failed to fetch NEO data');
                const data = await res.json();

                const today = new Date().toISOString().split('T')[0];
                const todayData = data.near_earth_objects[today] || [];

                const processed = todayData.map((item: any) => ({
                    id: item.id,
                    name: item.name,
                    date: item.close_approach_data[0].close_approach_date_full,
                    distanceVal: parseFloat(item.close_approach_data[0].miss_distance.kilometers),
                    distance: `${parseFloat(item.close_approach_data[0].miss_distance.kilometers).toLocaleString(undefined, { maximumFractionDigits: 0 })} km`,
                    velocityVal: parseFloat(item.close_approach_data[0].relative_velocity.kilometers_per_hour),
                    velocity: `${parseFloat(item.close_approach_data[0].relative_velocity.kilometers_per_hour).toLocaleString(undefined, { maximumFractionDigits: 0 })} km/h`,
                    diameterVal: item.estimated_diameter.meters.estimated_diameter_max,
                    diameter: `${Math.round(item.estimated_diameter.meters.estimated_diameter_max)}m`,
                    isHazardous: item.is_potentially_hazardous_asteroid,
                    absolute_magnitude: item.absolute_magnitude_h
                }));

                setNeos(processed);
            } catch (err: any) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };

        fetchNeos();
    }, []);

    const addToWatchlist = async (neo: Neo, e: React.MouseEvent) => {
        e.stopPropagation(); // Prevent opening modal
        if (!session) {
            router.push('/login');
            return;
        }

        try {
            const res = await fetch('/api/watchlist', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    asteroidId: neo.id,
                    asteroidName: neo.name,
                }),
            });

            if (res.ok) {
                alert(`Added ${neo.name} to watchlist!`);
            } else if (res.status === 409) {
                alert(`${neo.name} is already in your watchlist.`);
            } else {
                throw new Error('Failed to add');
            }
        } catch (error) {
            alert('Error adding to watchlist');
        }
    };

    const filteredAndSortedNeos = useMemo(() => {
        let result = [...neos];

        if (filterHazardous) {
            result = result.filter(n => n.isHazardous);
        }

        if (filterSize !== 'all') {
            result = result.filter(n => {
                if (filterSize === 'small') return n.diameterVal < 100;
                if (filterSize === 'medium') return n.diameterVal >= 100 && n.diameterVal < 500;
                if (filterSize === 'large') return n.diameterVal >= 500;
                return true;
            });
        }

        result.sort((a, b) => {
            if (sortBy === 'diameter') return b.diameterVal - a.diameterVal;
            if (sortBy === 'velocity') return b.velocityVal - a.velocityVal;
            if (sortBy === 'distance') return a.distanceVal - b.distanceVal; // closest first
            if (sortBy === 'date') return new Date(a.date).getTime() - new Date(b.date).getTime();
            return 0;
        });

        return result;
    }, [neos, filterHazardous, sortBy]);

    if (loading) {
        return (
            <div className="flex justify-center items-center p-8">
                <Loader2 className="w-8 h-8 text-cyan-glow animate-spin" />
            </div>
        );
    }

    if (error) {
        return (
            <div className="text-alert-red p-4 border border-alert-red/20 rounded bg-alert-red/10">
                Error loading feed: {error}
            </div>
        );
    }

    return (
        <div className="space-y-4 relative">
            <div className="flex flex-col space-y-4 mb-4">
                <div className="flex items-center justify-between">
                    <h2 className="text-2xl font-black italic text-white tracking-wider flex items-center gap-2 drop-shadow-md">
                        <span className="text-star-lord-gold">///</span> NEO FEED
                    </h2>
                    <span className="px-3 py-1 bg-cyan-glow/20 border border-cyan-glow text-cyan-glow text-xs font-bold rounded-full animate-pulse shadow-[0_0_10px_rgba(0,245,212,0.4)]">
                        LIVE: {filteredAndSortedNeos.length} OBJECTS
                    </span>
                </div>

                <div className="flex flex-wrap items-center gap-3 p-4 rounded-xl bg-deep-space/60 border border-cyan-glow/30 backdrop-blur-xl shadow-[0_0_30px_rgba(0,245,212,0.15)] relative overflow-hidden group">
                    <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-nebula-purple via-cyan-glow to-star-lord-gold opacity-50" />

                    <button
                        onClick={() => setFilterHazardous(!filterHazardous)}
                        className={clsx(
                            "px-5 py-2.5 rounded-lg text-xs font-bold uppercase tracking-wider flex items-center transition-all duration-300 shadow-lg hover:scale-105 border",
                            filterHazardous
                                ? "bg-alert-red text-white border-alert-red shadow-[0_0_20px_rgba(255,0,84,0.6)]"
                                : "bg-white/5 text-starlight border-white/10 hover:bg-alert-red/20 hover:text-alert-red hover:border-alert-red"
                        )}
                    >
                        <Filter className="w-4 h-4 mr-2" />
                        {filterHazardous ? 'HAZARDOUS ONLY' : 'SHOW ALL'}
                    </button>

                    <div className="flex items-center gap-2 bg-black/20 p-1 rounded-lg border border-white/5">
                        <span className="text-[10px] text-cyan-glow uppercase font-black px-2">Size:</span>
                        {(['all', 'small', 'medium', 'large'] as const).map((size) => (
                            <button
                                key={size}
                                onClick={() => setFilterSize(size)}
                                className={clsx(
                                    "px-3 py-1.5 rounded-md text-[10px] font-black uppercase tracking-wider transition-all",
                                    filterSize === size
                                        ? "bg-nebula-purple text-white shadow-[0_0_10px_rgba(157,78,221,0.5)]"
                                        : "text-void-gray hover:text-white hover:bg-white/5"
                                )}
                            >
                                {size}
                            </button>
                        ))}
                    </div>

                    <div className="h-8 w-px bg-white/10 mx-2 hidden sm:block" />

                    <div className="flex items-center gap-2 overflow-x-auto no-scrollbar py-1">
                        <span className="text-xs text-cyan-glow uppercase font-black mr-2 tracking-widest drop-shadow-md">Sort Data:</span>

                        {[
                            { id: 'diameter', label: 'SIZE', icon: Maximize2 },
                            { id: 'velocity', label: 'SPEED', icon: Gauge },
                            { id: 'distance', label: 'DIST', icon: Ruler },
                            { id: 'date', label: 'DATE', icon: Calendar },
                        ].map((opt) => {
                            const Icon = opt.icon;
                            return (
                                <button
                                    key={opt.id}
                                    onClick={() => setSortBy(opt.id as any)}
                                    className={clsx(
                                        "px-4 py-2 rounded-md text-xs font-bold transition-all border flex items-center gap-2 uppercase tracking-wide",
                                        sortBy === opt.id
                                            ? "bg-cyan-glow/20 border-cyan-glow text-cyan-glow shadow-[0_0_15px_rgba(0,245,212,0.3)] bg-[radial-gradient(ellipse_at_center,_var(--tw-gradient-stops))] from-cyan-glow/20 to-transparent"
                                            : "bg-transparent border-white/10 text-void-gray hover:text-white hover:border-white/50 hover:bg-white/5"
                                    )}
                                >
                                    <Icon className="w-3 h-3" />
                                    {opt.label}
                                </button>
                            );
                        })}
                    </div>
                </div>
            </div>

            <div className="space-y-3 max-h-[600px] overflow-y-auto pr-2 custom-scrollbar pointer-events-auto">
                {filteredAndSortedNeos.map((neo) => (
                    <GlassCard
                        key={neo.id}
                        className="p-4 flex items-center justify-between group cursor-pointer hover:bg-white/5 transition-all border-l-4 border-l-transparent hover:border-l-cyan-glow"
                        onClick={() => setSelectedNeo(neo)}
                    >
                        <div className="flex flex-col">
                            <div className="flex items-center space-x-2">
                                <span className={clsx(
                                    "text-lg font-black tracking-wide transition-colors uppercase",
                                    neo.isHazardous ? "text-alert-red" : "text-white group-hover:text-cyan-glow"
                                )}>
                                    {neo.name}
                                </span>
                                {neo.isHazardous && (
                                    <AlertTriangle className="w-4 h-4 text-alert-red animate-pulse" />
                                )}
                            </div>
                            <div className="text-xs text-void-gray mt-1 flex space-x-4 font-mono">
                                <span className="flex items-center gap-1"><Maximize2 className="w-3 h-3 text-nebula-purple" /> {neo.diameter}</span>
                                <span className="flex items-center gap-1"><Ruler className="w-3 h-3 text-gamora-green" /> {neo.distance}</span>
                                <span className="flex items-center gap-1"><Gauge className="w-3 h-3 text-star-lord-gold" /> {neo.velocity}</span>
                            </div>
                        </div>

                        <div className="flex items-center space-x-3">
                            <button
                                onClick={(e) => addToWatchlist(neo, e)}
                                className="p-2 hover:bg-cyan-glow/20 rounded-full transition-colors group/btn relative z-20"
                                title="Add to Watchlist"
                            >
                                <Eye className="w-4 h-4 text-void-gray group-hover/btn:text-cyan-glow" />
                            </button>

                            <div className={clsx(
                                "hidden sm:block px-2 py-1 rounded text-xs font-black uppercase tracking-wider",
                                neo.isHazardous ? "bg-alert-red/20 text-alert-red border border-alert-red/30" : "bg-cyan-glow/20 text-cyan-glow border border-cyan-glow/30"
                            )}>
                                {neo.isHazardous ? 'DANGER' : 'SAFE'}
                            </div>
                            <ArrowRight className="w-4 h-4 text-void-gray group-hover:text-white transition-transform group-hover:translate-x-1" />
                        </div>
                    </GlassCard>
                ))}
            </div>

            {/* Details Modal */}
            {selectedNeo && (
                <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
                    <div
                        className="absolute inset-0 bg-black/80 backdrop-blur-md"
                        onClick={() => setSelectedNeo(null)}
                    />
                    <div className="relative z-10 w-full max-w-lg animate-in fade-in zoom-in duration-200 pointer-events-auto">
                        <GlassCard className="p-0 overflow-hidden border-cyan-glow/50 shadow-[0_0_50px_rgba(0,245,212,0.2)]">
                            <div className="p-6 border-b border-white/10 flex justify-between items-start bg-deep-space/50">
                                <div>
                                    <h3 className="text-3xl font-black text-white flex items-center gap-2 italic uppercase">
                                        {selectedNeo.name}
                                        {selectedNeo.isHazardous && <AlertTriangle className="w-6 h-6 text-alert-red animate-pulse" />}
                                    </h3>
                                    <p className="text-sm text-cyan-glow font-mono mt-1">ID: {selectedNeo.id}</p>
                                </div>
                                <button
                                    onClick={() => setSelectedNeo(null)}
                                    className="p-2 hover:bg-white/10 rounded-full transition-colors text-void-gray hover:text-white"
                                >
                                    <X className="w-6 h-6" />
                                </button>
                            </div>

                            <div className="p-6 space-y-6 bg-gradient-to-b from-deep-space/80 to-nebula-purple/20">
                                <div className="grid grid-cols-2 gap-4">
                                    <div className="p-4 rounded-xl bg-white/5 border border-white/10 hover:border-cyan-glow/30 transition-colors">
                                        <div className="text-xs text-cyan-glow uppercase tracking-wider mb-2 font-bold flex items-center gap-2">
                                            <Ruler className="w-3 h-3" /> Miss Distance
                                        </div>
                                        <div className="text-xl font-mono text-white tracking-tight">{selectedNeo.distance}</div>
                                    </div>
                                    <div className="p-4 rounded-xl bg-white/5 border border-white/10 hover:border-alert-red/30 transition-colors">
                                        <div className="text-xs text-alert-red uppercase tracking-wider mb-2 font-bold flex items-center gap-2">
                                            <Gauge className="w-3 h-3" /> Velocity
                                        </div>
                                        <div className="text-xl font-mono text-white tracking-tight">{selectedNeo.velocity}</div>
                                    </div>
                                    <div className="p-4 rounded-xl bg-white/5 border border-white/10 hover:border-star-lord-gold/30 transition-colors">
                                        <div className="text-xs text-star-lord-gold uppercase tracking-wider mb-2 font-bold flex items-center gap-2">
                                            <Maximize2 className="w-3 h-3" /> Diameter
                                        </div>
                                        <div className="text-xl font-mono text-white tracking-tight">{selectedNeo.diameter}</div>
                                    </div>
                                    <div className="p-4 rounded-xl bg-white/5 border border-white/10 hover:border-nebula-purple/30 transition-colors">
                                        <div className="text-xs text-nebula-purple uppercase tracking-wider mb-2 font-bold flex items-center gap-2">
                                            <Eye className="w-3 h-3" /> Magnitude
                                        </div>
                                        <div className="text-xl font-mono text-white tracking-tight">{selectedNeo.absolute_magnitude}</div>
                                    </div>
                                </div>

                                <div className="p-5 rounded-xl bg-deep-space border border-sidebar-border shadow-inner">
                                    <div className="text-xs text-void-gray uppercase tracking-wider mb-2 font-bold">Close Approach Date</div>
                                    <div className="text-2xl text-white font-black italic">{selectedNeo.date}</div>
                                </div>

                                <div className="flex justify-end gap-3 pt-2">
                                    <button
                                        onClick={(e) => addToWatchlist(selectedNeo, e)}
                                        className="px-4 py-2 bg-gradient-to-r from-cyan-glow to-galaxy-blue text-deep-space font-black uppercase rounded hover:scale-105 transition-transform"
                                    >
                                        + Add to Watchlist
                                    </button>
                                    <a
                                        href={`https://ssd.jpl.nasa.gov/tools/sbdb_lookup.html#/?sstr=${selectedNeo.id}`}
                                        target="_blank"
                                        rel="noopener noreferrer"
                                        className="px-4 py-2 border border-white/20 hover:border-white text-white font-bold uppercase rounded transition-colors text-xs flex items-center"
                                    >
                                        NASA JPL Data &rarr;
                                    </a>
                                </div>
                            </div>
                        </GlassCard>
                    </div>
                </div>
            )}
        </div>
    );
}
